const IcongL = styled.img`
  height: 24px;
  width: 24px;
`;

function App() {
  const calculateTimeLeft = () => {
    let year = new Date().getFullYear();
    const difference = +new Date(`${year}-10`) - +new Date();
    let timeLeft = {};

    if (difference > 0) {
      timeLeft = {
        days: Math.floor(difference / (1000 * 60 * 60 * 24)),
        hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
        minutes: Math.floor((difference / 1000 / 60) % 60),
        seconds: Math.floor((difference / 1000) % 60),
      };
    }

    return timeLeft;
  };

  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());

  useEffect(() => {
    setTimeout(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000);
  });

  const timerComponents = <span>
        {timeLeft.hours}{": "}{timeLeft.minutes}{": "}{timeLeft.seconds}
      </span>;


  return (
    <Main>
      <Header>
        <Row style={{ paddingBottom: 16 }}>
          <Col xs={6}>
            <Link to={"/purchase"}><IcongL src={arrow} /></Link>
          </Col>
          <Col xs={6} style={{textAlign: 'right'}}>
            <Link to={"/purchase"}><StyledButton>STORE</StyledButton></Link>
          </Col>
        </Row>
        <H1Text>In the Spotlight</H1Text>
        <H4Text>New products designed to brighten your day, brought to you every day !</H4Text>
      </Header>
      <Content>
        <ProductWrapper>
          <ProductImage
            src={spotlight}
            alt="First slide"
          />
        </ProductWrapper>
        <Desc>
          <Row>
            <Col xs={8}> <H3Text>Stoneware Face Pot</H3Text></Col>
            <Col xs={{span: 4}} style={{textAlign: 'right'}}>
              <H3GreenText>₹1,000</H3GreenText>
            </Col>
          </Row>
          <H6LightGreyText>By Madriguera workshop</H6LightGreyText>
          <H6Text style={{paddingTop: 8}}>They are made by hand in stoneware. Our Face collection will turn 1 year old
            soon more...</H6Text>
          <span>
          <Icon src={timer}/>
          <H6OrangeText>Expires in {timerComponents}</H6OrangeText>
        </span>
          <Separator className="rounded"/>
          <Row>
            <Col xs={6}> <H1Bold>Order Timeline</H1Bold></Col>
            <Col xs={6} style={{textAlign: 'right'}}>
              <H6Text>Supporters: 54,546</H6Text>
            </Col>
          </Row>
          <Slider
            src={slider}
            alt="First slide"
          />
          <Separator className="rounded"/>
          <H6GreenBoldText>Limites Stocks Available</H6GreenBoldText>
          <Row style={{ paddingTop: 4 }}>
            <Col xs={8}>
              <H6Text>Products remaining - 12,332</H6Text>
            </Col>
            <Col xs={4} style={{textAlign: 'right'}}>
              <H6GreenText>Price: ₹1000</H6GreenText>
            </Col>
          </Row>
          <Separator className="rounded"/>
          <Container>
            <Row>
              <Col xs={6} style={{ padding: 0 }}>
                <Icon src={fav}/>
                <Icon src={share} style={{ marginLeft: 4 }}/>
                <Icon src={comment} style={{ marginLeft: 6 }}/>
              </Col>
              <Col xs={{span: 6}} style={{textAlign: 'right', padding: 0}}>
                <Link to={"/purchase"}><StyledButton>PLACE ORDER</StyledButton></Link>
              </Col>
            </Row>
          </Container>
          <Separator className="rounded"/>
          <H6LightGreyText>View all 11 reviews</H6LightGreyText>
        </Desc>
      </Content>
    </Main>
  );
}

export default App;
